// app/[sportName]/page.js (LIMPIADO)
import styles from '../page.module.css';
import SportSection from '../../components/SportSection'; 
import SportSubNav from '../../components/SportSubNav'; 
import Link from 'next/link';

// Datos estáticos que definen la apariencia de la sección
const sportAppearanceData = {
    'futbol': { display: "Fútbol", deporte: "Futbol", imagen: "/images/cancha_de_futbol.jpg", sectionClass: styles.sportFutbol, buttonClass: styles.btnFutbol },
    'voleibol': { display: "Vóleibol", deporte: "Voleibol", imagen: "/images/cancha_de_voley.jpg", sectionClass: styles.sportVoleibol, buttonClass: styles.btnVoleibol },
    'basquetbol': { display: "Básquetbol", deporte: "Basquetbol", imagen: "/images/cancha_de_basquetbol.webp", sectionClass: styles.sportBasquetbol, buttonClass: styles.btnBasquetbol },
};

async function getLigasData() {
    const baseUrl = process.env.NODE_ENV === 'production' 
                    ? 'https://tu-dominio.com'
                    : 'http://localhost:3000'; 
                    
    const res = await fetch(`${baseUrl}/api/ligas`, { 
        cache: 'no-store' 
    }); 

    if (!res.ok) {
        throw new Error('Fallo al obtener datos de Google Sheets.');
    }

    return res.json();
}

export default async function SportPage(props) {
    
    const sportId = props.params.sportName;
    const appearanceData = sportAppearanceData[sportId];

    if (!appearanceData) {
        return (
            <main className={styles.main}>
                <div className={styles.errorContainer}>
                    <h1>Error 404</h1>
                    <p>El deporte "{sportId}" no existe o no está activo.</p>
                    <Link href="/" className={styles.welcomeButton}>Volver al Inicio</Link>
                </div>
            </main>
        );
    }
    
    // --- LÓGICA DE DATOS (Aún se llama para no romper otras funciones, pero no se usa la lista) ---
    try {
        await getLigasData();
    } catch (error) {
        console.error("Error al obtener datos de API:", error);
    }

    return (
        <main className={styles.main}>
            {/* CONTENIDO JSX */}
            <div className={styles.whiteContentWrapper}> 
                
                {/* 1. ENCABEZADO SUPERIOR */}
                <header className={styles.secondaryHeader}>
                    <Link href="/" className={styles.backButton}>← Inicio</Link>
                </header>

                {/* 2. SUB-NAVEGACIÓN DE DEPORTE */}
                <SportSubNav 
                    sportNameId={sportId} 
                    sportDisplayName={appearanceData.display} 
                />

                {/* 3. CONTENIDO PRINCIPAL DEL DEPORTE */}
                <div className={styles.sportContentArea}>
                    
                    <h2 className={styles.sportContentTitle}>Partidos y Fechas de {appearanceData.display}</h2>
                    
                    <SportSection 
                        sportName={appearanceData.deporte} 
                        imageSrc={appearanceData.imagen} 
                        imageAlt={`Cancha de ${appearanceData.deporte}`} 
                        sectionClass={styles.sportSectionWhite} 
                        buttonClass={styles.btnPartidosRed} 
                    />

                    <section className={styles.sportDetails}>
                        <h3>Noticias destacadas (Portada)</h3>
                        <p>Aquí irá el contenido principal de la portada de {appearanceData.display}.
                           Para ver la lista de equipos, haz clic en **"Equipos"** en la barra de navegación.</p>
                    </section>
                </div>
                
            </div>
            
        </main>
    );
}